@import url(https://fonts.googleapis.com/css?family=Barlow+Semi+Condensed&display=swap);
body{
  margin: 0;
  font-family: 'Barlow Semi Condensed', sans-serif;
  background: radial-gradient(hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  color: #DDD;
  max-height: 100%;
}
html{
  height:100%;
}
.main-container{
    margin-top: 25px;
}
@-webkit-keyframes fade-in{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes fade-in{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.game-container{
    display: flex;
    justify-content: center;
    flex-flow: wrap;
    max-width:500px;
    height: auto;
    margin: auto;
    padding: 30px;
    z-index: -0;
    -webkit-animation: fade-in 0.5s forwards;
            animation: fade-in 0.5s forwards;
}
.rock{
    background: radial-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
}
.paper{
    background: radial-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
}
.scissor{
    background: radial-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}
.game-option-wrap{
    cursor: pointer;
    float: left;
    position: relative;
    width: 175px;
    height: 175px;
    border-radius: 50%;
    margin: 15px 30px;
    z-index: -0;
    box-shadow: inset  0 -7px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}
.game-option-wrap:hover{
    box-shadow: inset  0 -10px 0 rgba(0,0,0,0.2);
}
.game-option{
    position: absolute;
    width: 130px;
    height: 130px;
    top: 12.5%;
    left: 12.5%;
    -webkit-clip-path: circle(65px at 50% 50%);
            clip-path: circle(65px at 50% 50%);
    border-radius: 50%;
    background: #DDD;
    z-index: -2;
    box-shadow: inset  0 7px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}
.game-option:hover{
    box-shadow: inset  0 10px 0 rgba(0,0,0,0.2);
}
.game-option > img{
    position: absolute;
    left: 30%;
    top: 30%;
}
.game-option-border{
    position: absolute;
    width: 150px;
    height: 150px;
}
.game-background{
    display: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    position: absolute;
    max-width:500px;
    height: auto;
}
.bar-container{
    position: relative;
    margin: auto;
    max-width:500px;
    z-index: -7;
}
.game-bar{
    margin: 15px 30px;
    height: 175px;
}
.bar-1{
    flex: 1 1;
    flex-grow: 2;
    position: absolute;
    width: 100%;
    height: 15px;
    background: rgba(0,0,0,0.2);
    z-index: -5;
}
.bar-2{
    position: absolute;
    width: 50%;
    height: 15px;
    bottom: 0;
    left: 0;
    transform: rotate(45deg);
    background: rgba(0,0,0,0.2);
    z-index: -5;
}
.bar-3{
    position: absolute;
    width: 50%;
    height: 15px;
    bottom: 0;
    right: 0;
    transform: rotate(-45deg);
    background: rgba(0,0,0,0.2);
    z-index: -5;
}

@media only screen and (max-width: 620px) {
    .game-container{
        width: 100%;
        padding: 0;
    }
    .game-option-wrap{
        width: 120px;
        height: 120px;
    }

    .game-option{
        width: 90px;
        height: 90px;
    }
    .game-option img{
        left: 20%;
        top: 20%;   
    }
}
@-webkit-keyframes appearAnimation{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0.8;
    }
    75%{
        opacity: 0.9;
    }
    100%{
        opacity: 1;
    }
}

@keyframes appearAnimation{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0.8;
    }
    75%{
        opacity: 0.9;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes appearHousePick{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes appearHousePick{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@-webkit-keyframes appearResult{
    0%{
        flex: unset;
        max-width: 0;
        opacity: 0;
    }
    80%{
        opacity: 0.0;
    }
    100%{
        max-width: 300px;
        opacity: 1;
    }
}
@keyframes appearResult{
    0%{
        flex: unset;
        max-width: 0;
        opacity: 0;
    }
    80%{
        opacity: 0.0;
    }
    100%{
        max-width: 300px;
        opacity: 1;
    }
}

.game-container-results{
    display: flex;
    justify-content: center;
    flex-flow: wrap;
    max-width:100%;
    height: auto;
    margin: auto;
    padding: 30px;
    z-index: -0;
    -webkit-animation: appearAnimation 1s;
            animation: appearAnimation 1s;
}
.rock{
    background: radial-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
}
.paper{
    background: radial-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
}
.scissor{
    background: radial-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}
.game-option-wrap-results{
    float: left;
    position: relative;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    margin: 15px 30px;
    z-index: -0;
    box-shadow: inset  0 -10px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}
.game-option-wrap-results:hover{
    box-shadow: inset  0 -13px 0 rgba(0,0,0,0.2);
}
.game-option-container-results{
    position: relative;
}
.win{
}
.game-option-results{
    position: absolute;
    width: 200px;
    height: 200px;
    top: 10%;
    left: 10%;
    -webkit-clip-path: circle(100px at 50% 50%);
            clip-path: circle(100px at 50% 50%);
    border-radius: 50%;
    background: #DDD;
    z-index: -2;
    box-shadow: inset  0 10px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}
.game-option-results:hover{
    box-shadow: inset  0 13px 0 rgba(0,0,0,0.2);
}
.game-option-results > img{
    position: absolute;
    width: 90px;
    left: 27%;
    top: 25%;
}
.game-option-border{
    position: absolute;
    width: 150px;
    height: 150px;
}
.pick{
    text-transform: uppercase;
    text-align: center;
    font-weight: 700;
    font-size: 20px;
}
.title{
    margin: 30px 0;
}
.house{
    opacity: 0;
    -webkit-animation: appearHousePick 1s 1s forwards;
            animation: appearHousePick 1s 1s forwards;
}
.bg-housepick{
    opacity: 0.2;
    position: absolute;
    width: 200px;
    height: 200px;
    top: 40px;
    left: 55px;
    border-radius: 50%;
    background: #DDD;
    z-index: -2;
    box-shadow: inset  0 10px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    background: rgb(0,0,0);
}
.game-result{
    margin-top: 40px;
    display:flex;
    flex-flow: column;
    justify-content: center;
    flex: 1 1;
    text-align:center;
    text-transform: uppercase;
    max-width: 0;
    opacity: 0;
    -webkit-animation: appearResult 1.5s 1.2s forwards;
            animation: appearResult 1.5s 1.2s forwards;
}
.text-result{
    margin: 10px 0;
    font-size: 42px;
    font-weight: 700;
}
.replay-button{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin: 10px 0;
    background: #FFF;
    border-radius: 10px;
    color: hsl(349, 70%, 56%);
    font-weight: 700;
    padding: 15px;
    transition: 0.3s;
}
.replay-button:hover{
    cursor: pointer;
    color: hsl(177, 65%, 36%);
    background: #FFF;
}

@media only screen and (max-width: 620px) {
    .game-container-results{
        width: 100%;
        padding: 0;
    }
    .game-option-wrap-results{
        box-shadow: inset  0 -5px 0 rgba(0,0,0,0.2);
        width: 120px;
        height: 120px;
    }

    .bg-housepick{
        width: 120px;
        height: 120px;
        top: 15px;
        left: 30px;
    }
    .game-option-results{
        box-shadow: inset  0 5px 0 rgba(0,0,0,0.2);
        width: 95px;
        height: 95px;
    }
    .game-option-results img{
        left: 20%;
        width: unset;
        top: 20%;   
    }
    .pick{
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
        font-size: 16px;
    }
    .title{
        justify-content: center;
        position: fixed;
        top: 260px;
        margin-left: -5px;
        width: 50%;
    }
    .game-result{
        position: fixed;
        top:300px;
    }
    .replay-button{
        color: hsl(229, 25%, 31%);
    }
}
.container{
    max-width: 600px;
    height: 100px;
    border: 1px solid hsl(217, 16%, 45%);
    border-radius: 10px;
    padding: 10px;
    
    margin:auto;
}
.switch-language img{
    height: 20px;
}
.switch-language{
    display: inline-block;
    position: fixed;
    height: 20px;
    top:0;
    left: 0;
    background-color: #EEE;
    color: #333;
    border: 1px solid #BBB;
    border-radius: 0 0 10px 0;
    padding: 3px 3px;
    transition: 0.4s;
}
.switch-language:hover{
    cursor: pointer;
    background-color: RGBA(0,0,0,0.4);
    color: rgb(90, 63, 63);
}
.game-title{
    float: left;
    font-size: 33px;
    font-weight: 700;
    line-height: 1em;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    text-transform: uppercase;
}

.game-scoreboard{
    float: right;
    padding: 0 20px;
    background-color: #EEE;
    text-align: center;
    line-height: 2.5em;
    color: #333;
    border: 1px solid #333;
    border-radius: 10px;
}
.scoreboard-text{
    font-size:20px;
}
.scoreboard-score{
    font-size: 62px;
    font-weight: 700;
    color: hsl(229, 25%, 31%);
}

@media only screen and (max-width: 620px) {
    .container{
        margin: 20px;
        padding: 10px;
        height: 75px;
    }
    .game-title{
        font-size: 25px;
    }
    .game-scoreboard{
        line-height: 2em;
    }
    .scoreboard-text{
        font-size: 18px;
    }
    .scoreboard-score{
        font-size: 42px;
    }
}
.rules-container {
    display: inline-block;
    text-transform: uppercase;
    font-weight: 700;
    position: fixed;
    bottom: 0;
    right: 0;
    border: 1px solid #BBB;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 20px 40px;
    cursor: pointer;
    transition: 0.4s;
}

.rules-container:hover {
    background-color: #EEE;
    color: #222;
}

.github-link {
    display: inline-block;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #EEE;
    color: #333;
    border: 1px solid #BBB;
    border-radius: 0 10px 0 0;
    padding: 0 20px;
    transition: 0.4s;
}

.github-link:hover {
    cursor: pointer;
    background-color: RGBA(0, 0, 0, 0.4);
    color: #EEE;
}

.modalBody {
    margin: auto;
    width: 304px;
}

.rules-close {
    cursor: pointer;
    float: right;
    color: hsl(217, 16%, 45%);
}

.Modal {
    text-transform: uppercase;
    font-weight: 700;
    max-width: 400px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    max-height: 400px;
    margin: auto;
    background-color: #EEE;
    color: #222;
    border: 1px solid #DDD;
    border-radius: 7px;
    padding: 0 10px;
}

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: RGBA(0, 0, 0, 0.5);
}

.ReactModal__Overlay {
    opacity: 0;
    transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModal__Overlay--before-close {
    opacity: 0;
}

@media only screen and (max-width: 620px) {
    .rules-container {
        font-size: 14px;
        text-align: center;
        left: 0;
        bottom: 5px;
    }

    .Modal {
        padding: 0;
        border-radius: 0;
        width: 100%;
        max-height: 100%;
    }

    .modalBody {
        padding: 20px 0;
        text-align: center;
    }

    .modalHeader {
        text-align: center;
        margin: 10px;
    }

    .rules-close {
        position: fixed;
        bottom: 20px;
        text-align: center;
        width: 100%;
        margin: 0 -10px;
        cursor: pointer;
        color: hsl(217, 16%, 45%);
    }
}
